import firebase from 'firebase/app'

export default {
  state: {
    users: [],
    info: {},
  },
  mutations: {
    setInfo(state, info) {
      state.info = info
    },
    setUsers(state, info) {
      state.info = info
    },
    clearInfo(state) {
      state.info = {}
    },
    loadInfo(state, item) {
      state.users = item
    },
  },
  actions: {
    async fetchInfo({dispatch, commit}) {
      try {
        const uid = await dispatch('getUid')
        const info = (await firebase.database().ref(`/users/${uid}/info`).once('value')).val()
        commit('setInfo', info)
      } catch (e) {
        commit('setError', e)
        throw e
      }
    },
    
    async fetchUser({commit}) {
      try {
        const comment = (await firebase.database().ref('/users').once('value')).val() || {}
        return Object.keys(comment).map(key => ({...comment[key], id: key}))
      } catch (e) {
        commit('setError', e)
        throw e
      } 
    },
    async fetchShoesById({commit}, id) {
      try{
        const shoe = (await firebase.database().ref(`/users`).child(id).once('value')).val() || {}
        return {...shoe, id}
      } catch(e) {
        commit('setError', e)
        throw e
      }
    },
    async updateInfo({commit}, {id,  instagram, tiktok, telegram, facebook, photo}) {
      try {
        await firebase.database().ref('/users').child(id).update({instagram, tiktok, telegram, facebook})
        await firebase.storage().ref('/users').child(id).update({photo})
      } catch (e) {
        commit('setError', e)
        throw e
      } 
    },
  },
  getters: {
    info: s => s.info,
    users: s => s.users,
  }
}