<template>
  <div class="container mx-auto">
    <div class="grid grid-cols-4 gap-4 mt-8">
      <form @submit.prevent="submitHandler" class="w-full max-w-lg col-span-1 xl:col-span-2 lg:col-span-2">
        <div class="flex flex-wrap -mx-3 mb-6">
          <div class="w-full md:w-full px-3 mb-6 md:mb-0">
            <label class="block uppercase tracking-wide text-gray-700 text-xs font-bold mb-2" for="grid-first-name">
              Логин
            </label>
            <input v-model="email" class="appearance-none block w-full bg-gray-200 text-gray-700 border border-red-500 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white" id="grid-first-name" type="text" placeholder="Jane">
            <p class="text-red-500 text-xs italic">Please fill out this field.</p>
          </div>
        </div>
        <div class="flex flex-wrap -mx-3 mb-6">
          <div class="w-full px-3">
            <label class="block uppercase tracking-wide text-gray-700 text-xs font-bold mb-2" for="grid-password">
              Пароль
            </label>
            <input v-model="password" class="appearance-none block w-full bg-gray-200 text-gray-700 border border-gray-200 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white focus:border-gray-500" id="grid-password" type="password" placeholder="*******">
            <p class="text-gray-600 text-xs italic">Make it as long and as crazy as you'd like</p>
          </div>
        </div>
        <div class="flex flex-wrap -mx-3 mb-2">
          <div class="w-full md:w-1/3 px-3 mb-6 md:mb-0">
            <label class="block uppercase tracking-wide text-gray-700 text-xs font-bold mb-2" for="grid-city">
              Data
            </label>
            <input v-model="date" class="appearance-none block w-full bg-gray-200 text-gray-700 border border-gray-200 rounded py-3 px-4 leading-tight focus:outline-none focus:bg-white focus:border-gray-500" id="grid-city" type="text" placeholder="10/10/2020">
          </div>
        </div>
        <div class="mt-6">
          <label>
            <input class="mr-2" type="checkbox" v-model="agree" />
            <span class="mr-6">С правилами согласен</span>
          </label>
          <button type="submit" class="bg-green-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded">Создать</button>
          <a class="write-1" @click="logout">Log out</a>
        </div>
      </form>
      <div class="lg:col-span-2 xl:col-span-2 col-span-1 w-full overflow-auto chart ">
        <li class="flex my-4 " v-for="u in users" :key="u.id">
          <div class="grid grid-cols-3 gap-4 w-full">
            <div class="col-span-1 ">
              <h2>{{ u.info.email }}</h2>
            </div>
            <div class="col-span-1 text-center">
              <h2>{{ u.info.date }}</h2>
            </div>
            <div class="col-span-1 text-center">
              <router-link :key="u.id" :to="{ name: 'Member', params: { id: u.id }, }"  tag="a" class="text-blue-500">
                <i class="fas fa-download text-2xl"></i>
              </router-link>
            </div>
          </div>
        </li>
      </div>
    </div>
    
  </div>
</template>
<script>
  export default {
    data: () => ({
      email: '',
      password: '',
      agree: false,
      date: '',
      users: []
    }),
    async mounted() {
      this.users = await this.$store.dispatch('fetchUser')
    },
    methods: {
      async submitHandler() {
        const formData = {
          email: this.email,
          password: this.password,
          date: this.date
        }
        try {
          await this.$store.dispatch('register', formData)
          this.$router.go('/')
        } catch (e) {
          e
        }
      },
      async logout() {
        await this.$store.dispatch('logout')
        this.$router.go('/')
      }
    }
  }
</script>
<style scoped>
  .chart {
    height: 600px;
    border: 2px solid grey;
    border-radius: 4px;
    padding: 5px 20px;
  }
</style>