<template>
  <div class="bg h-full w-full">
    <div class="container mx-auto text-center">
      <div class="grid grid-cols-6 gap-4">
        <div class="col-span-3 ml-10  mt-4 sm:col-span-2 md:col-span-3 lg:col-span-4 xl:col-span-2">
          <img class="bg-gray-700 ml-4 mt-10 w-32 h-32 rounded-full border-none" :src="selectedFile" alt="">
          <label class="icon-file text-center flex ml-2 mt-4 p-2 bg-green-500 rounded-lg" for="file-input">
            <i class="fas fa-upload ml-4"></i>
            <p class="ml-4">Загрузить</p>
          </label>
          <input id="file-input" class="img-input" type="file" @change="onFileSelected">
        </div>
        <div class="col-span-2 sm:col-span-4 md:col-span-3 lg:col-span-2 xl:col-span-4">
          <div class="mt-24">
            <h2 class="text-2xl font-semibold text-gray-700">{{ info.email }}</h2>
          </div>
        </div>
      </div>
      <div class="grid-cols-1 mt-8">
        <form class="w-full max-w-lg">
          <div class="w-full px-3 text-left">
            <h4 class="block tracking-wide ml-2 mb-2">{{ info.email }}</h4>
            <input v-model="instagram" class="appearance-none block w-full bg-gray-200 text-gray-700 border border-gray-200 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white focus:border-gray-500" id="grid-password" type="text" placeholder="Instagram URL">
          </div>
        </form>
      </div>
      <div class="grid-cols-1 mt-8">
        <form class="w-full max-w-lg">
          <div class="w-full px-3 text-left">
            <h4 class="block tracking-wide ml-2 mb-2">{{ info.email }}</h4>
            <input v-model="tiktok" class="appearance-none block w-full bg-gray-200 text-gray-700 border border-gray-200 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white focus:border-gray-500" id="grid-password" type="text" placeholder="TikTok URL">
          </div>
        </form>
      </div>
      <div class="grid-cols-1 mt-8">
        <form class="w-full max-w-lg">
          <div class="w-full px-3 text-left">
            <h4 class="block tracking-wide ml-2 mb-2">{{ info.email }}</h4>
            <input v-model="telegram" class="appearance-none block w-full bg-gray-200 text-gray-700 border border-gray-200 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white focus:border-gray-500" id="grid-password" type="text" placeholder="Telegram URL">
          </div>
        </form>
      </div>
      <div class="grid-cols-1 mt-8">
        <form class="w-full max-w-lg">
          <div class="w-full px-3 text-left">
            <h4 class="block tracking-wide ml-2 mb-2">{{ info.email }}</h4>
            <input v-model="facebook" class="appearance-none block w-full bg-gray-200 text-gray-700 border border-gray-200 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white focus:border-gray-500" id="grid-password" type="text" placeholder="Facebook URL">
          </div>
        </form>
      </div>
      <button @click="onUpload" class="bg-green-500 h-12 w-32 rounded-lg mt-8 mb-8 text-white">Изменить</button>
      
    </div>
  </div>
</template>
<script>
export default {
  metaInfo: {
    title: `User`,
  },
  data: () => ({
    selectedFile: null,
    instagram: '',
    telegram: '',
    tiktok: '',
    facebook: ''
  }),
  async mounted() {
    this.info = await this.$store.dispatch('fetchInfo')
  },
  methods: {
    onFileSelected(event) {
      const file = event.target.files[0];
      this.selectedFile = URL.createObjectURL(file)
    },
    async onUpload() {
      const userInfo = {
        instagram: this.instagram,
        telegram: this.telegram,
        facebook: this.facebook,
        tiktok: this.tiktok,
        photo: this.selectedFile

      }
      await this.$store.dispatch('updateInfo', userInfo)
    }
  },
  computed: {
    info() {
      return this.$store.getters.info
    }
  },
}
</script>
<style scoped>
  .img-input {
    display: none;
  }
  .icon-file {
    width: 160px;
    
    color: white;
    font-size: 18px;
  }
  
</style>