<template>
  <div>
    <h2>{{ user.info.email }}</h2>
    <h2>{{ user.info.date }}</h2>

  </div>
</template>
<script>
export default {
  data: () => ({
    user: []
  }),
  async mounted() {
    const id = this.$route.params.id
    this.user = await this.$store.dispatch('fetchShoesById', id)
  }
}
</script>